import React from 'react';

const Scroll = (props) => {
	let displayGira ="dib";
	if(props.props.titoloLoaded)
		displayGira = "displayNone"

	if(props.props.check===0&&props.props.type!=='mobile'){
		return (
			<div id="Scrollamelo" className="overflowY" style={	{
							height:`${props.props.inHeight-109}px`,}}>
			{props.children}
			<div id="giraeriggira" className={`giraeriggira ${displayGira}`}></div>
			</div>
			);
	} else if (props.props.type==='mobile'){
												if(props.props.check===0){



															return (
																<div id="Scrollamelo" className="overflowY" style={	{
																				height:`${props.props.inHeight-67}px`,}}>
																{props.children}
																<div id="giraeriggira" className={`giraeriggira ${displayGira}`}></div>
																</div>
															);}else{

																return (
																<div id="Scrollamelo" className="overflowY" style={	{
																				height:`${props.props.inHeight-67}px`,}}>
																{props.children}
																<div id="giraeriggira" className={`giraeriggira ${displayGira}`}></div>
																</div>
															);
															}
	} else{



		return (
			<div id="Scrollamelo" className="overflowY" style={	{
							height:`${props.props.inHeight-109}px`,}}>
			{props.children}
			<div id="giraeriggira" className={`giraeriggira ${displayGira}`}></div>


			</div>
		);


	}


};

export default Scroll;



